.login {
	h1 {
		a {
			background-image: none;
			width: auto;
			overflow: visible;
			text-indent: 0;
			height: 90px;
			position: relative;
			outline: none;
		}
		img {
			position: absolute;
			display: block;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
			object-position: center;
		}
	}
}